/* reset css */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root, * {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background: none;
  color: white;
  cursor: pointer;
}

header, .get-early-access__email {
  background-color: #1c2431;
}

main {
  background-color: #181f2a;
}

h1, h3 {
  font-family: "Raleway";
}

body {
  width: 100%;
  height: 100vh;
}
body header.header .header__container {
  padding: 3rem 2.3rem 0 2.3rem;
}
body header.header .header__container nav.header__nav {
  display: flex;
  justify-content: space-between;
  color: white;
}
body header.header .header__container nav.header__nav img {
  display: inline-block;
  width: 8rem;
  height: 2.5rem;
}
body header.header .header__container nav.header__nav ul.nav__nav-list {
  display: flex;
  gap: 1.9rem;
  font-size: 1.27rem;
  font-weight: 400;
  opacity: 0.8;
}
body header.header .header__intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.8rem;
  padding: 5rem 3rem 5rem 3rem;
  text-align: center;
  color: white;
  background-image: url("/images/bg-curvy-mobile.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left -33rem bottom -5rem;
}
body header.header .header__intro img {
  display: inline-block;
  width: 100%;
}
body header.header .header__intro h1.intro__heading {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.5;
}
body header.header .header__intro p.intro__description {
  font-weight: 300;
  line-height: 1.6;
}
body header.header .header__intro a button {
  margin-top: 0.9rem;
  padding: 1.7rem 8.5rem;
  background: linear-gradient(to right, #65e2d9, #339ecc);
  border-radius: 3rem;
}

@media screen and (min-width: 1440px) {
  body header.header .header__container {
    padding: 7.2rem 8.1rem 0 8.1rem;
  }
  body header.header .header__container nav.header__nav img {
    width: 17.8rem;
    height: 5rem;
  }
  body header.header .header__container nav.header__nav ul.nav__nav-list {
    margin-top: 1.5rem;
    font-size: 1.6rem;
  }
  body header.header .header__container nav.header__nav ul.nav__nav-list li:hover {
    font-weight: 700;
    text-decoration: underline;
  }
  body header.header .header__container nav.header__nav ul.nav__nav-list li:not(:first-of-type) {
    margin-left: 3.7rem;
  }
  body header.header .header__intro {
    gap: 2rem;
    padding: 10rem 30rem 0 32rem;
    background-image: url(/images/bg-curvy-desktop.svg);
    background-size: contain;
    background-position: left -5rem bottom -18rem;
  }
  body header.header .header__intro img {
    width: 71.6rem;
    height: 53.8rem;
  }
  body header.header .header__intro h1.intro__heading {
    font-size: 3.8rem;
  }
  body header.header .header__intro p.intro__description {
    margin-top: 1.2rem;
    padding: 0 9.7rem 0 11.9rem;
    font-size: 2rem;
  }
  body header.header .header__intro a button {
    padding: 2rem 10rem;
    font-size: 1.6rem;
    transform: translateX(5%);
  }
  body header.header .header__intro a button:hover {
    background: #95dbe4;
  }
}